<!-- 한페이지에서 사졌다 나왔다 하는거 -->
<template>
  <div id="wrap">
    <fruitHeader/>

    <div id="card" v-if="bVisible === true">
      <div class="fruitCard" :key="fruit.code" v-for="fruit in fruits">
        <img :src="fruit.img" alt="" @click="detailShow" />
        <p>{{ fruit.title }}</p>
        <p>{{ fruit.cost }}</p>
        <button @click="costChange">{{ text }}</button>
      </div>
    </div>

    <!-- 상세페이지 -->
    <div id="subCard" v-if="bVisible === false">
      <!-- v-if="bVisible === false" -> v-else 로 가능 -->
      <div>
        <img :src="require(`@/assets/` + detailImg)" alt="" /> 
        <p>{{ detailName }}</p>
        <p>{{ detailPrice }}</p>
        <button @click="costChangeOne">{{ text }}</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      bVisible: true,
      fruits: [
        {
          img: require("@/assets/apple.png"),
          title: "Apple",
          cost: "$3.65",
          code: "01",
        },
        {
          img: require("@/assets/banana.png"),
          title: "Banana",
          cost: "$7.99",
          code: "02",
        },
        {
          img: require("@/assets/cherries.png"),
          title: "Cherries",
          cost: "$2.93",
          code: "03",
        },
        {
          img: require("@/assets/lemon.png"),
          title: "Lemon",
          cost: "$1.2",
          code: "04",
        },
        {
          img: require("@/assets/mango.png"),
          title: "Mango",
          cost: "$10",
          code: "05",
        },
        {
          img: require("@/assets/tangerine.png"),
          title: "Tangerine",
          cost: "$4.79",
          code: "06",
        },
        {
          img: require("@/assets/watermelon.png"),
          title: "Watermelon",
          cost: "$2.4",
          code: "07",
        },
        {
          img: require("@/assets/strawberry.png"),
          title: "Strawberry",
          cost: "$5",
          code: "08",
        },
        {
          img: require("@/assets/avocado.png"),
          title: "Avocado",
          cost: "$6.4",
          code: "09",
        },
        {
          img: require("@/assets/pineapple.png"),
          title: "Pineapple",
          cost: "$15",
          code: "10",
        },
      ],
      text: "Add To Cart",
    };
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    costChange(event) {
      // 버튼 (Add to Cart)의 상위노드
      const thisNode = event.target.parentNode;

      //아이템 선택적 현재 총 가격
      let crntTotPrice = 0;
      const chPrice = document.querySelector("#price");
      crntTotPrice = chPrice.textContent.split("$")[1];
      //$0에서 0에 값이 들어가야하니까 쪼개서 인덱스로

      //선택한 아이템의 가격   //cost가 인덱스2번이라
      let price = thisNode.childNodes[2].textContent.split("$")[1];

      //선택한 아이템들의 총합계
      let itemTotSum = 0;
      itemTotSum = Number(crntTotPrice) + Number(price);
      itemTotSum = itemTotSum.toFixed(2); //소수점 2자리까지만 표시

      console.log("아이템 선택 전 현재 총 가격:" + crntTotPrice);
      console.log("선택한 아이템의 가격 :" + price);
      console.log("선택한 아이템까지의 총 합계:" + itemTotSum);

      // 총합계를 #price에 출력
      chPrice.textContent = "$" + itemTotSum.toString();
    },
    detailShow(event) {
      // 버튼 (Add to Cart)의 상위노드
      const thisNode = event.target.parentNode;

      // //이미지를 선택했을때 가격 0 되기
      // const chPrice = document.querySelector('#price');
      // chPrice.textContent = '$'+ 0;

      //자식노드의 이미지 주소를 가져옴(예, http://localhost8080/img/apple.5b54646.png)
      console.log(thisNode.childNodes[0].src);

      //자식노드 중에 바인딩된 파일면만 잘라서 가져옴(예, apple.5b54646.png)
      this.detailImg = thisNode.childNodes[0].src.split("/")[4];
      //주소에서 비인딩값은 제거하고 이미지 파일명만만 잘라 붙임(예, apple.png)
      this.detailImg =
        this.detailImg.split(".")[0] + "." + this.detailImg.split(".")[2];
      console.log(this.detailImg);

      this.detailName = thisNode.childNodes[1].textContent;
      this.detailPrice = thisNode.childNodes[2].textContent;

      this.bVisible = false;
    },
    costChangeOne(event) {
      const thisNode = event.target.parentNode;

      let crntTotPrice = 0;
      const chPrice = document.querySelector("#price");
      crntTotPrice = chPrice.textContent.split("$")[1];
      console.log("아이템 선택 전 현재 총 가격:" + crntTotPrice);

      let price = thisNode.childNodes[2].textContent.split("$")[1];
      console.log("선택한 아이템의 가격 :" + price);

      let itemTotSum = 0;
      itemTotSum = Number(crntTotPrice) + Number(price);
      itemTotSum = itemTotSum.toFixed(2); //소수점 2자리까지만 표시

      console.log("아이템 선택 전 현재 총 가격:" + crntTotPrice);
      console.log("선택한 아이템의 가격 :" + price);
      console.log("선택한 아이템까지의 총 합계:" + itemTotSum);

      chPrice.textContent = "$" + itemTotSum.toString();
    },
  },
};
</script>

<style scoped>
button {
  margin: 1vw;
  background: rgb(206, 35, 35);
  border: none;
  border-radius: 5px;
  padding: 1.5vw;
  color: white;
  font-weight: bold;
}
.fruitCard {
  float: left;
  margin: 1vw;
  background: white;
  border-radius: 5px;
  box-shadow: 2px 2px rgba(173, 173, 173, 0.607);
}
#subCard {
  margin: 1vw;
  background: white;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 2px rgba(173, 173, 173, 0.607);
}
</style>
