<!-- router-link로 연결 -->
<template>
  <div>
    <section :key="fruit.code" v-for="fruit in fruits">
      <div id="fruitCard" >
       <router-link to="/fruitsub"><img @click="pageChange" :src="fruit.img" alt="" /></router-link>
        <p>{{ fruit.title }}</p>
        <p>{{ fruit.cost }}</p>
        <!-- 배열은 공간으로 만들어주고 인식해야함(br은 공간은 안됌) -->
        <button @click="costChange">{{ text }}</button>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      fruits: [
        {
          img: require("@/assets/apple.png"),
          title: "Apple",
          cost: "$3.65",
          code: "01"
        },
        {
          img: require("@/assets/banana.png"),
          title: "Banana",
          cost: "$7.99",
          code: "02"
        },
        {
          img: require("@/assets/cherries.png"),
          title: "Cherries",
          cost: "$2.93",
          code: "03"
        },
        {
          img: require("@/assets/lemon.png"),
          title: "Lemon",
          cost: "$1.2",
          code: "04"
        },
        {
          img: require("@/assets/mango.png"),
          title: "Mango",
          cost: "$10",
          code: "05"
        },
        {
          img: require("@/assets/tangerine.png"),
          title: "Tangerine",
          cost: "$4.79",
          code: "06"
        },
        {
          img: require("@/assets/watermelon.png"),
          title: "Watermelon",
          cost: "$2.4",
          code: "07"
        },
        {
          img: require("@/assets/strawberry.png"),
          title: "Strawberry",
          cost: "$5",
          code: "08"
        },
        {
          img: require("@/assets/avocado.png"),
          title: "Avocado",
          cost: "$6.4",
          code: "09"
        },
        {
          img: require("@/assets/pineapple.png"),
          title: "Pineapple",
          cost: "$15",
          code: "10"
        },
      ],
      text: "Add To Cart",
    };
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    costChange(event){
      // 버튼 (Add to Cart)의 상위노드
      const thisNode = event.target.parentNode;

      //아이템 선택적 현재 총 가격
      let crntTotPrice =0;
      const chPrice = document.querySelector('#price');
      crntTotPrice = chPrice.textContent.split('$')[1];
      //$0에서 0에 값이 들어가야하니까 쪼개서 인덱스로

      //선택한 아이템의 가격   //cost가 인덱스2번이라
      let price = thisNode.childNodes[2].textContent.split('$')[1];

      //선택한 아이템들의 총합계
      let itemTotSum = 0;
      itemTotSum = Number(crntTotPrice)+Number(price);
      itemTotSum = itemTotSum.toFixed(2) //소수점 2자리까지만 표시

      console.log('아이템 선택 전 현재 총 가격:' + crntTotPrice);
      console.log('선택한 아이템의 가격 :' +price);
      console.log('선택한 아이템까지의 총 합계:' + itemTotSum);

      // 총합계를 #price에 출력
      chPrice.textContent = '$' + itemTotSum.toString();
    },
    // pageChange(event){
    //   const thisN = event.target.parentNode;

    //   const subFC = document.querySelector('#subFruitCard');
    //   let subImg = document.getElementById('subImg');
      
    //   let a = thisN.childNodes[2];
      
    //   console.log(subFC)
    //   onsole.log(subImg)
    //   onsole.log(a)

    // }

  },
};
</script>
<style scoped>
  div{
    background: beige;
  }
section {
    float: left;
}
/* img{
  width: 20vw;
} */
button{
  /* width: 28vw; */
  margin: 1vw;
  background: rgb(206, 35, 35);
  border: none;
  border-radius: 5px;
  padding: 1.5vw;
  color: white;
  font-weight: bold;
}
#fruitCard {
  margin: 1vw;
  background: white;
  border-radius: 5px;
  box-shadow: 2px 2px rgba(173, 173, 173, 0.607); 
}
</style>
