import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/mission',
    name: 'mission',
    component: () => import(/* webpackChunkName: "mission" */ '../views/missionView.vue')
  },
  {
    path: '/mission2',
    name: 'mission2',
    component: () => import(/* webpackChunkName: "mission2" */ '../views/mission2View.vue')
  },
  {
    path: '/fruitpage',
    name: 'fruitpage',
    component: () => import(/* webpackChunkName: "fruit" webpackPrefetch:true  */ '../views/fruitPageView.vue')
  },
  {
    path: '/fruitsub',
    name: 'fruitsub',
    component: () => import(/* webpackChunkName: "fruit" webpackPrefetch:true  */ '../views/fruitSubView.vue')
  },
  {
    path: '/uservue',
    name: 'uservue',
    component: () => import(/* webpackChunkName: "fruit" webpackPrefetch:true  */ '@/components/userVue/User.vue')
  },
  {
    path: '/uservueex',
    name: 'uservueex',
    component: () => import(/* webpackChunkName: "fruit" */ '@/components/userVueEx/User.vue')
  },
  {
    path: '/newpage',
    name: 'newpage',
    component: () => import(/* webpackChunkName: "fruit" */ '../views/newPageView.vue')
  },
  {
    path: '/bootstrap',
    name: 'bootstrap',
    component: () => import(/* webpackChunkName: "fruit" */ '../views/bootstrapView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
