import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import BootstrapVue3 from 'bootstrap-vue-3'
// 뷰3.0에 설치할때 사용

// 전역으로 등록할 컴포넌트를 import한다.
import status from './components/status'
import newMission from './components/newMission'
import mHeader from './components/mHeader'
import mSection from './components/mSection'
import mFooter from './components/mFooter'
import mAside from './components/mAside'
import mArticle from './components/mArticle'
import fruitHeader from './components/fruitHeader'
import fruitSection from './components/fruitSection'
import fruitFooter from './components/fruitFooter'
import fruitSubArticle from './components/fruitSubArticle'
import fruitSub from './components/fruitSub'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue-3/dist/bootstrap-vue-3.css'

import bootstrap from './components/bootstrap/bootHeader'

//처음시도한 두개의 영역으로 잡은것
// import imgPage from './components/imgPage'
// import newPage from './components/newPage'
// import newPage2 from './components/newPage2'

//컴포넌트 등록
const app = createApp(App)

//app.component('사용시 호출할 이름', 등록할 컴포넌트)
//사용시 호출할 이름 ->다른파일에서 태그로 사용할 이름
app.component('status', status)
app.component('newMission', newMission)
app.component('mHeader', mHeader)
app.component('mSection', mSection)
app.component('mFooter', mFooter)
app.component('mAside', mAside)
app.component('mArticle', mArticle)
app.component('fruitHeader', fruitHeader)
app.component('fruitSection', fruitSection)
app.component('fruitFooter', fruitFooter)
app.component('fruitSubArticle', fruitSubArticle)
app.component('fruitSub', fruitSub)
app.component('bootstrap', bootstrap)

 
// app.component('newPage', newPage)
// app.component('newPage2', newPage2)
// app.component('imgPage', imgPage)


// app.mount('#app')

app.use(store).use(BootstrapVue3).use(router).mount('#app')




